import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import HeaderTop from './HeaderTop';
//@ts-ignore
import { HashLink } from 'react-router-hash-link';


interface P {
	y: number
	VisibleInd: number,
	SectionInd: Record<string, number>,
	openNav: string,
	set_openNav: Function,
	NavBName: string,
	set_NavBName: Function
}

const HeaderDashBoard: React.FC<P> = ({ y, VisibleInd, SectionInd, openNav, set_openNav, NavBName, set_NavBName }) => {

	const [stickClass, set_NavSticky] = useState('topnav-container')
	const [sticky, set_Sticky] = useState(Number.MAX_VALUE)

	useEffect(() => {
		const nav = document.getElementById('topnav-stick')
		//@ts-ignore
		set_Sticky(nav.offsetTop)
	}, [])

	useEffect(() => {
		if (!window.matchMedia("(max-width: 767px)").matches) {
			if (y >= sticky) {
				set_NavSticky("topnav-container-sticky")
			} else {
				set_NavSticky("topnav-container");
			}
		}
	})

	return <div id={stickClass} className="header">
		<HeaderTop />
		<div id='topnav-stick' className={stickClass}>
			<div className="topnav" style={{ height: openNav }}>
				<Link to="/">
					<img src={process.env.PUBLIC_URL + '/images/home.svg'} alt='Missing png' style={{ filter: "invert(1)", height: "20px", width: "20px", padding: "5px" }}></img>
                    Home
				</Link>
				<HashLink className={String(VisibleInd === SectionInd['Income'])} to="#Income">
					<img src={process.env.PUBLIC_URL + '/images/line.png'} alt='Missing png' style={{ width: "40px", padding: "5px" }}></img>
					Line Charts
				</HashLink>
				<HashLink className={String(VisibleInd === SectionInd['MissedPayments'])} to="#MissedPayments">
					<img src={process.env.PUBLIC_URL + '/images/bar.png'} alt='Missing png' style={{ height: "35px", paddingBottom: "3px" }}></img>
					Bar Charts
				</HashLink>
			</div>
		</div>
		<div id={NavBName} className='Nav_Button' onClick={() => {
			if (openNav == '0vh') { set_openNav('30vh'); set_NavBName('Close') }
			else { set_openNav('0vh'); set_NavBName('Navigation') }
		}}>	<div className='Burger'></div>
			<div className='Burger2'></div>
			<div className='Burger3'></div>
		</div>
	</div>

}
export default HeaderDashBoard
