import React, { useEffect, useRef, useState } from 'react'
import * as d3 from 'd3'
import * as helper from '../helperFunctions'
import { max, min, tree } from 'd3'
import * as panel from './panelData'
import * as filter from 'components/Filters/Filters'
import IncomeCombFilt from 'components/Filters/IncomeCombFilt'

interface P {
	width: number
	height: number
	cardInd: number
	data: RootReducerState['raw_data']
	isVisible: boolean
	section: string
}
const TestIncome: React.FC<P> = ({ width, height, data, cardInd, isVisible, section }) => {

	const [filter_val, set_filter] = useState("Overall")
	const [filter_Comb, set_filterComb] = useState("None")
	const [filter_Income, set_filterIncome] = useState("F0")
	const [filter_Education, set_filterEducation] = useState("None")
	const [filter_List, set_filterList] = useState({
		type: 'None',
		filters: ['']
	})

	const [filter_RDD, set_filterRDD] = useState("RDD")
	const [openFilter, set_openFilter] = useState('0vh')
	const [graphicFilter, set_graphic] = useState(999)
	let margin = { top: 60, right: 20, bottom: 20, left: 45 }
	let innerWidth = width - margin.left - margin.right
	let innerHeight = height - margin.top - margin.bottom;

	function Income(index: number) {

		let f_area = filter_val
		let f_inc = filter_Income
		let f_edu = filter_Education

		let plotTitle = ''
		let legend: Record<string, string> = {}
		let legendDot: Record<string, string> = {}
		let linearScale: any;
		let bandScale: any;

		const navy: string = '#344a62'
		const grey: string = 'grey'
		const orange: string = 'orange'
		const Colors: string[] = [navy, orange, grey]

		const yLabel = 'Percentage (%)'
		const xLabel = 'Months'
		const g = d3.select('#IncomeSVG')
		const gNodes = g.select('.nodes')
		const gLinks = g.select('.links')


		let passedList: [string, number][][] = []
		let passedExpList: [string, number][][] = []

		let toolList: Record<string, Record<string, string>> = {}
		let toolListInc: Record<string, Record<string, string>> = {}
		let toolListSpend: Record<string, Record<string, string>> = {}
		if (filter_List.type != 'None') {

			let filt = ''

			for (let i = 0; i < filter_List.filters.length; i++) {
				filt = filter_List.filters[i]
				switch (filter_List.type) {
					case 'Education':
						f_edu = filt
						if (index == 0) {
							plotTitle = 'Line Graph 1'
						}
						if (index == 1) {
							plotTitle = 'Line Graph 2'
						}
						if (index == 2) {
							plotTitle = 'Line Graph 3'
						}
						else if ((index === 3)) {
							plotTitle = 'Line Graph 4'
						}
						legend['Legend 1'] = Colors[0]
						legend['Legend 2'] = Colors[1]
						legend['Legend 3'] = Colors[2]

						singleLine(Colors[i], i)

						break;
					case 'Income':
						const legendMap: Record<string, string> = {
							'F1': 'Less than 17.5k',
							'F23': 'Greater than 17.5k'
						}
						f_inc = filt
						if (index == 0) {
							plotTitle = 'Line Graph 1'
						}
						if (index == 1) {
							plotTitle = 'Line Graph 2'
						}
						if (index == 2) {
							plotTitle = 'Line Graph 3'
						}
						else if ((index === 3)) {
							plotTitle = 'Line Graph 4'
						}
						legend[legendMap['Legend 1']] = Colors[0]
						legend[legendMap['Legend 2']] = Colors[1]

						singleLine(Colors[i], i)

						break;
					case 'Area':
						f_area = filt
						if (index == 0) {
							plotTitle = 'Line Graph 1'
						}
						if (index == 1) {
							plotTitle = 'Line Graph 2'
						}
						if (index == 2) {
							plotTitle = 'Line Graph 3'
						}
						else if ((index === 3)) {
							plotTitle = 'Line Graph 4'
						}
						legend['Legend 1'] = Colors[0]
						legend['Legend 2'] = Colors[1]
						singleLine(Colors[i], i)

						break;
					default:
						console.error('No Switch statement exists!')
				}
			}
		}
		else {
			if (index == 0) {
				plotTitle = 'Line Graph 1'
			}
			if (index == 1) {
				plotTitle = 'Line Graph 2'
			}
			if (index == 2) {
				plotTitle = 'Line Graph 3'
			}
			else if ((index === 3)) {
				plotTitle = 'Line Graph 4'
			}
			singleLine(navy, 0)
		}
		if (window.matchMedia("(max-width: 767px)").matches) {
			helper.plotTitle(g, innerWidth, plotTitle, true)
		} else {
			helper.plotTitle(g, innerWidth, plotTitle)
		}

		function singleLine(color: string, iter: number) {

			const IncomeAgg = panel.IncomeData({ data: data, filter_val: f_area, filter_Income: f_inc, filter_Education: f_edu, filter_RDD: filter_RDD })
			const Spending = panel.Spending({ data: data, filter_val: f_area, filter_Income: f_inc, filter_Education: f_edu, filter_RDD: filter_RDD })
			const IncomeCI: Record<number, number[]> = {}
			const eName: Record<string, number> = {
				'February': 1,
				'MayFeb': 4,
				'AugFeb': 7,
				'AugExp': 9
			}
			const month: Record<string, number> = {
				"January": 0,
				"February": 1,
				"March": 2,
				"April": 3,
				"May": 4,
				"June": 5,
				"July": 6,
				"August": 7,
				"September": 8,
				"October": 9,
				"November": 10,
				"December": 11,
				'MayFeb': 4,
				'AugFeb': 7,
				'AugExp': 9
			}

			Object.keys(IncomeAgg['Income']).forEach((e) => {
				if ((e === 'February') || (e === 'MayFeb') || (e === 'AugFeb') || (e === 'AugExp')) {
					IncomeCI[month[e]] = [helper.getConfInt(IncomeAgg['Income'][e])[0], helper.getConfInt(IncomeAgg['Income'][e])[1]]

				}
			});

			const IncomeMean: Record<number, number> = {};
			const ExpMean: Record<number, number> = {};

			if (IncomeAgg['Income']['February'].length === 0) {
				IncomeMean[1] = 1
			} else {
				IncomeMean[1] = IncomeAgg['Income']['February'].reduce((a: number, b: number) => a + b, 0) / IncomeAgg['Income']['February'].length;
				// [0, 1].forEach(i => IncomeCI[1][i] = (100 * IncomeCI[1][i] / IncomeMean[1]) - 100)
				// As Feb should have no confidence Interval
				[0, 1].forEach(i => IncomeCI[1][i] = 0)

			}
			Object.keys(IncomeAgg['Income']).forEach(e => {
				if ((e === 'MayFeb') || (e === 'AugFeb')) {
					if (IncomeAgg['Income'][e].length === 0) {
						IncomeMean[month[e]] = 0
					}
					else {
						IncomeMean[month[e]] = (100 * IncomeAgg['Income'][e].reduce((a: number, b: number) => (b + a), 0) / IncomeAgg['Income'][e].length) - 100;
						[0, 1].forEach(i => IncomeCI[month[e]][i] = (100 * IncomeCI[month[e]][i]) - 100)
					}
				}
				if ((e === 'AugFeb') || (e === 'AugExp')) {
					if (IncomeAgg['Income'][e].length === 0) {
						ExpMean[month[e]] = 0
					}
					else {
						ExpMean[month[e]] = (100 * IncomeAgg['Income'][e].reduce((a: number, b: number) => (b + a), 0) / IncomeAgg['Income'][e].length) - 100;
						if (e === 'AugExp') {
							[0, 1].forEach(i => IncomeCI[month[e]][i] = (100 * IncomeCI[month[e]][i]) - 100)
						}
					}
				}
			})

			const passedIncomeCI = Object.entries(IncomeCI)

			IncomeMean[1] = 0;

			const SpendingCI: Record<number, number[]> = {}
			Object.keys(Spending).forEach((e) => {
				if ((e !== 'FebruaryR1') && (e !== 'FebruaryR2')) {
					SpendingCI[month[e]] = [helper.getConfInt(Spending[e])[0], helper.getConfInt(Spending[e])[1]]
				}
			});

			const SpendingMean: Record<string, number> = {};
			const SpendingExpMean: Record<number, number> = {};

			if (Spending['February'].length === 0) {
				SpendingMean[1] = 1
			} else {
				SpendingMean[1] = Spending['February'].reduce((a: number, b: number) => a + b, 0) / Spending['February'].length;
				// [0, 1].forEach(i => SpendingCI[1][i] = (100 * SpendingCI[1][i] / SpendingMean[1]) - 100)
				[0, 1].forEach(i => SpendingCI[1][i] = 0)

			}
			Object.keys(Spending).forEach(e => {
				if ((e === 'May') || (e === 'August')) {
					if (Spending[e].length === 0) {
						SpendingMean[month[e]] = 0
					} else {
						SpendingMean[month[e]] = (100 * Spending[e].reduce((a: number, b: number) => (b + a), 0) / Spending[e].length) - 100;
						[0, 1].forEach(i => SpendingCI[month[e]][i] = (100 * SpendingCI[month[e]][i]) - 100)
					}
				}
				if ((e === 'August') || (e === 'AugExp')) {
					if (Spending[e].length === 0) {
						SpendingExpMean[month[e]] = 0
					}
					else {
						SpendingExpMean[month[e]] = (100 * Spending[e].reduce((a: number, b: number) => (b + a), 0) / Spending[e].length) - 100;
						if (e === 'AugExp') {
							[0, 1].forEach(i => SpendingCI[month[e]][i] = (100 * SpendingCI[month[e]][i]) - 100)
						}
					}
				}
			})
			const passedSpendingCI = Object.entries(SpendingCI)
			SpendingMean[1] = 0;

			const tooltipTable: Record<string, Record<string, string>> = {}
			Object.keys(IncomeAgg['Income']).forEach(e => {

				let percentile_25 = Math.round(0.25 * (IncomeAgg['Income'][e].length)) - 1;
				let percentile_50 = Math.round(0.5 * (IncomeAgg['Income'][e].length)) - 1;
				let percentile_75 = Math.round(0.75 * (IncomeAgg['Income'][e].length)) - 1;

				if (e === 'February') {
					tooltipTable[month[e] + '#' + String(iter)] = {
						'Value': IncomeMean[month[e]].toFixed(2) + '%',//
						'Mean': (IncomeAgg['Income'][e].reduce((a: number, b: number) => a + b, 0) / IncomeAgg['Income'][e].length).toFixed(2),
						'25 %': String((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_25]),
						'50 %': String((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_50]),
						'75 %': String((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_75]),
						'Count': String(IncomeAgg['Income'][e].length)
					};
				}
				else if ((e === 'MayFeb') || (e === 'AugFeb') || (e === 'AugExp')) {
					let FebRMean: number = 0;
					if (e === 'MayFeb') {

						FebRMean = IncomeAgg['Income']['FebruaryR1'].reduce((a: number, b: number) => a + b, 0) /
							IncomeAgg['Income']['FebruaryR1'].length
					}
					else if ((e == 'AugFeb') || (e == 'AugExp')) {

						FebRMean = IncomeAgg['Income']['FebruaryR2'].reduce((a: number, b: number) => a + b, 0) /
							IncomeAgg['Income']['FebruaryR2'].length
					}
					if (e !== 'AugExp') {

						tooltipTable[month[e] + '#' + String(iter)] = {
							'Value': IncomeMean[month[e]].toFixed(2) + '%',
							'Mean': ((IncomeMean[month[e]] / 100) * FebRMean).toFixed(2),
							'25 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_25] * 100).toFixed(2) + '%',
							'50 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_50] * 100).toFixed(2) + '%',
							'75 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_75] * 100).toFixed(2) + '%',
							'Count': String(IncomeAgg['Income'][e].length)
						};
					}
					else {
						tooltipTable[month[e] + '#' + String(iter)] = {
							'Value': ExpMean[month[e]].toFixed(2) + '%',
							'Mean': ((ExpMean[month[e]] / 100) * FebRMean).toFixed(2),
							'25 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_25] * 100).toFixed(2) + '%',
							'50 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_50] * 100).toFixed(2) + '%',
							'75 %': ((IncomeAgg['Income'][e]).sort((a: number, b: number) => a - b)[percentile_75] * 100).toFixed(2) + '%',
							'Count': String(IncomeAgg['Income'][e].length)
						};
					}
				}
			})

			toolListInc = Object.assign({}, toolListInc, tooltipTable);

			const tooltipSpendTable: Record<string, Record<string, string>> = {}
			Object.keys(Spending).forEach(e => {

				let percentile_25 = Math.round(0.25 * (Spending[e].length)) - 1;
				let percentile_50 = Math.round(0.5 * (Spending[e].length)) - 1;
				let percentile_75 = Math.round(0.75 * (Spending[e].length)) - 1;
				if ((e !== 'FebruaryR1') && (e !== 'FebruaryR2')) {
					if (e === 'February') {

						tooltipSpendTable[month[e] + '#' + String(iter)] = {
							'Value': SpendingMean[month[e]].toFixed(2) + '%',
							'Mean': (Spending[e].reduce((a: number, b: number) => a + b, 0) / Spending[e].length).toFixed(2),
							'25 %': String((Spending[e]).sort((a: number, b: number) => a - b)[percentile_25]),
							'50 %': String((Spending[e]).sort((a: number, b: number) => a - b)[percentile_50]),
							'75 %': String((Spending[e]).sort((a: number, b: number) => a - b)[percentile_75]),
							'Count': String(Spending[e].length)
						};
					}
					else {
						let FebRMean: number = 0;
						if (e === 'May') {

							FebRMean = Spending['FebruaryR1'].reduce((a: number, b: number) => a + b, 0) /
								Spending['FebruaryR1'].length
						}
						else if ((e === 'August') || (e === 'AugExp')) {

							FebRMean = Spending['FebruaryR2'].reduce((a: number, b: number) => a + b, 0) /
								Spending['FebruaryR2'].length
						}
						if (e !== 'AugExp') {
							tooltipSpendTable[month[e] + '#' + String(iter)] = {
								'Value': SpendingMean[month[e]].toFixed(2) + '%',
								'Mean': ((SpendingMean[month[e]] / 100) * FebRMean).toFixed(2),
								'25 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_25] * 100).toFixed(2) + '%',
								'50 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_50] * 100).toFixed(2) + '%',
								'75 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_75] * 100).toFixed(2) + '%',
								'Count': String(Spending[e].length)
							};
						}
						else {
							tooltipSpendTable[month[e] + '#' + String(iter)] = {
								'Value': SpendingExpMean[month[e]].toFixed(2) + '%',
								'Mean': ((SpendingExpMean[month[e]] / 100) * FebRMean).toFixed(2),
								'25 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_25] * 100).toFixed(2) + '%',
								'50 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_50] * 100).toFixed(2) + '%',
								'75 %': ((Spending[e]).sort((a: number, b: number) => a - b)[percentile_75] * 100).toFixed(2) + '%',
								'Count': String(Spending[e].length)
							};
						}
					}
				}
			})
			toolListSpend = Object.assign({}, toolListSpend, tooltipSpendTable);

			let passedData: [string, number][];
			let ExpPassedData: [string, number][];

			helper.generateElements([['line', 1]], 'LineArea', gLinks, 'path');
			if (filter_List.type === 'None') {
				g.selectAll('.LineArea')
					.transition().duration(500)
					.style('opacity', 0.2);
			}
			else {
				g.selectAll('.LineArea')
					.transition().duration(500)
					.style('opacity', 0)
			}

			if ((index < 4)) {

				toolList = toolListInc
				passedData = Object.entries(IncomeMean);
				ExpPassedData = Object.entries(ExpMean);
				// [linearScale, bandScale] = helper.generateAxis(Object.keys(eName),
				// 	[Math.min(-45, 1.3 * Number(min(Object.values(IncomeMean)))), Math.max(15, 1.3 * Number(max(Object.values(IncomeMean))))], [0, innerWidth], [innerHeight, 0])
				// bandScale.padding(1)

				if (iter === 0) {

					linearScale =
						d3.scaleLinear()
							.domain([Math.min(-45, 1.3 * Number(min(Object.values(IncomeMean)))), Math.max(15, 1.3 * Number(max(Object.values(IncomeMean))))])
							.range([innerHeight, 0])
					bandScale =
						d3.scaleTime()
							//@ts-ignore
							.domain([new Date(2020, 0, 1), new Date(2020, 10, 31)])
							.range([0, innerWidth])
				}

				passedList.push(passedData)
				passedExpList.push(ExpPassedData)

				if (filter_List.type === 'None') {
					legend = {
						'Legend': color,
					}
				}

				legendDot = {
					'Expected': navy
				}

				if (filter_List.type === 'None') {

					let area: any = d3.area().x((d) => bandScale(new Date(2020, d[0], 1)))
						//@ts-ignore
						.y0((d) => linearScale(d[1][0]))
						//@ts-ignore
						.y1((d) => linearScale(d[1][1]))

					g.selectAll('.LineArea')
						.datum(passedIncomeCI)
						.transition().delay(500)
						.transition().duration(500)
						.attr("d", area);
				}
			}

			else if ((index === 4)) {

				toolList = toolListSpend
				passedData = Object.entries(SpendingMean);
				ExpPassedData = Object.entries(SpendingExpMean);

				// [linearScale, bandScale] = helper.generateAxis(Object.keys(eName),
				// 	[Math.min(-45, 1.3 * Number(min(Object.values(IncomeMean)))), Math.max(15, 1.3 * Number(max(Object.values(SpendingMean))))], [0, innerWidth], [innerHeight, 0])
				// bandScale.padding(1)

				if (iter === 0) {

					linearScale =
						d3.scaleLinear()
							.domain([Math.min(-45, 1.3 * Number(min(Object.values(SpendingMean)))), Math.max(15, 1.3 * Number(max(Object.values(SpendingMean))))])
							.range([innerHeight, 0])
					bandScale =
						d3.scaleTime()
							//@ts-ignore
							.domain([new Date(2020, 0, 1), new Date(2020, 10, 31)])
							.range([0, innerWidth])
				}

				passedList.push(passedData)
				passedExpList.push(ExpPassedData)

				if (filter_List.type === 'None') {
					legend = {
						'Legend': color
					}
				}

				legendDot = {
					'Expected': navy
				}

				let area: any = d3.area().x((d) => bandScale(new Date(2020, d[0], 1)))
					//@ts-ignore
					.y0((d) => linearScale(d[1][0]))
					//@ts-ignore
					.y1((d) => linearScale(d[1][1]))

				g.selectAll('.LineArea')
					.datum(passedSpendingCI)
					.transition().delay(500)
					.transition().duration(500)
					.attr("d", area)
			}

			const xAxis = d3.axisBottom(bandScale)
				.tickValues([new Date(2020, 0, 1), new Date(2020, 1, 1), new Date(2020, 4, 1), new Date(2020, 7, 1),
				new Date(2020, 9, 1), new Date(2020, 11, 1)])
				//@ts-ignore
				.tickFormat(d3.timeFormat("%b"));

			const tick: any = ""

			helper.showXAxis(xAxis, g, innerHeight, innerWidth, xLabel)
			helper.showXGrid(xAxis.tickSize(-innerHeight).tickFormat(tick), g)

			const yAxis = d3.axisLeft(linearScale)
			helper.showYAxis(yAxis, g, innerHeight, innerWidth, yLabel, margin)
			helper.showYGrid(yAxis.tickSize(-innerWidth).tickFormat(tick), g)

			g.select('.yGrid')
				.selectAll('g.tick')
				.filter((d) => d == 0)
				.select('line')
				.style('stroke-width', 2)
				.style('stroke', 'red')
		}

		helper.makeLineMonth({
			gLinks: gLinks, bandScale: bandScale,
			linearScale: linearScale, passedList: passedList
		})
		helper.makeLineMonth({
			gLinks: gLinks, bandScale: bandScale,
			linearScale: linearScale, passedExpList: passedExpList,
			type: 'Exp'
		})

		const nLegends = Object.entries(legend).length
		helper.showLegend(legend, g)
		helper.showLegendDot(legendDot, g, true, nLegends)

		if (filter_List.type !== 'None') {
			g.selectAll('.legend')
				.attr('transform', `translate(${bandScale(new Date(2020, 11, 1)) - 130},${linearScale(linearScale.domain()[0] + 14)})`)
		} else {
			g.selectAll('.legend')
				.attr('transform', `translate(${bandScale(new Date(2020, 11, 1)) - 80},${linearScale(linearScale.domain()[0] + 13)})`)
		}
		let combinedList: [string, number][] = []
		let nLoop = 0
		passedList.forEach((e) => {
			e.forEach((i) => {
				combinedList.push([i[0] + '#' + String(nLoop), i[1]])
			})
			nLoop += 1
		})
		nLoop = 0
		passedExpList.forEach((e) => {
			e.forEach((i) => {
				combinedList.push([i[0] + '#' + String(nLoop), i[1]])
			})
			nLoop += 1
		})
		helper.highlightLine({ data: combinedList, selection: gNodes, bandScale: bandScale, linearScale: linearScale, tooltipTable: toolList, month: true })

		g.attr('transform', `translate(${margin.left},${margin.top})`)
	}

	let activeIndex = 0;
	let lastIndex = -1;

	const scrollVis = (index: number) => {
		const g = d3.select('#IncomeSVG')

		helper.createGrid(g, innerHeight)
		helper.createAxis(g, innerHeight)
		helper.generateElements([['legend', 1]], 'legend', g, 'g')
		helper.generateElements([['links', 1]], 'links', g, 'g')
		helper.generateElements([['nodes', 1]], 'nodes', g, 'g')

		activeIndex = index;

		const sign = (activeIndex - lastIndex) < 0 ? -1 : 1;
		const scrolledSections = [activeIndex]

		scrolledSections.forEach(function (i) {
			Income(i);
		});
		lastIndex = activeIndex;
	};

	const pan = d3.select('.' + section)

	useEffect(() => {
		if (cardInd === 1) {
			set_filterComb('F1')
		}
		else if (cardInd === 2) {
			set_filterComb('Education')
		}
		else if (cardInd === 3) {
			set_filterComb('Area')
		}
		else {
			set_filterComb('None')
		}
		set_filterRDD("RDD")
	}, [cardInd, isVisible]);

	useEffect(() => {
		set_filter('Overall')
		set_filterIncome('F0')
		set_filterEducation('None')
		set_filterList({
			type: 'None',
			filters: []
		})
		if (filter_Comb != 'None') {

			if ((filter_Comb === 'Rural') || (filter_Comb === 'Urban')) {
				set_filter(filter_Comb)
			}
			else if ((filter_Comb === 'F1') || (filter_Comb === 'F2')) {
				set_filterIncome(filter_Comb)
			}
			else if (filter_Comb === 'Education') {
				set_filterList({
					type: filter_Comb,
					filters: ['Dropped out Before Matric', 'Matric', 'More than Matric']
				})
			}
			else if (filter_Comb === 'Income') {
				set_filterList({
					type: filter_Comb,
					filters: ['F1', 'F23']
				})
			}
			else if (filter_Comb === 'Area') {
				set_filterList({
					type: filter_Comb,
					filters: ['Rural', 'Urban']
				})
			}
			else {
				set_filterEducation(filter_Comb)
			}
		}
	}, [filter_Comb]);
	// const EduBucket: Record<string, number[]> = {
	// 	'Dropped out Before Matric': [1, 2],
	// 	'Matric': [3],
	// 	'More than Matric': [4, 5, 6, 7, 8, 9]
	// }
	useEffect(() => {

		(window.matchMedia("(max-width: 767px)").matches) ?
			set_openFilter('0vh') : set_openFilter('auto');
		if (isVisible) {
			pan.selectAll('.card')
				.style('opacity', function (d, i) { return i === cardInd ? 1 : 0.3; });

			d3.select('#IncomeSVG')
				.style('opacity', 1);
			scrollVis(cardInd);
		} else {
			pan.selectAll('.card')
				.style('opacity', 0.4);

			d3.select('#IncomeSVG')
				.style('opacity', 0.4);
		}
		set_graphic(999)
	}, [isVisible, cardInd, data, width, height, filter_val, filter_Income, filter_Education, filter_RDD, filter_List]);

	return <div id='Income' className="section" style={{ width: `${width}`, height: `${((height + 120) * 5) + 400}px` }}>
		<div id='graphic' className={section} style={{ zIndex: graphicFilter }}>
			<div id="0" className="card" >
				<div className="content">
					Lorem ipsum dolor sit amet, consectetur adipiscing elit.
					Duis facilisis suscipit dui accumsan mattis.
				</div>
			</div>
			<div id="1" className="card" >
				<div className="content">
					Lorem ipsum dolor sit amet, consectetur adipiscing elit.
					Duis facilisis suscipit dui accumsan mattis.
				</div>
			</div>
			<div id="2" className="card" >
				<div className="content">
					Lorem ipsum dolor sit amet, consectetur adipiscing elit.
					Duis facilisis suscipit dui accumsan mattis.
				</div>
			</div>
			<div id="3" className="card" >
				<div className="content">
					Lorem ipsum dolor sit amet, consectetur adipiscing elit.
					Duis facilisis suscipit dui accumsan mattis.
				</div>
			</div>
			{/* <div id="4" className="card" >
				<div className="content">
					SAVINGS<br /><br />The Average savings decreased in May to 86% of
					what it was in February.
				</div>
			</div>
			<div id="5" className="card" >
				<div className="content">
					SAVINGS (Feb Income Below Min Wage)<br /><br />People with income less than 17.5k have
					been most succesful in achieving a savings rate similar to that of February.
				</div>
			</div> */}
		</div>
		<div id='vis' style={{ height: height + 120 }} >
			<svg width={width} height={height + 20} >
				<g id='IncomeSVG' />
			</svg>
			<div id="filter" style={{ gridTemplateColumns: 'repeat(2, 1fr)', height: openFilter, width: width }}>
				<span className="close" onClick={() => { set_openFilter('0vh'); set_graphic(999) }}>&times;</span>
				<IncomeCombFilt set_filterIncomeComb={set_filterComb} value={filter_Comb} />
				<filter.RDDFilter set_filter={set_filterRDD} value={filter_RDD} />
			</div>
			<button className='Filter_Button' onClick={() => {
				if (openFilter == '0vh') {
					set_openFilter('20vh'); set_graphic(-1)
				} else {
					set_openFilter('0vh'); set_graphic(999)
				}
			}}>Filters</button>
		</div>
	</div >
}

export default TestIncome;